import React from 'react';

import './input-route.scss';

const InputRoute = () => {
	return (
		<div className={'input-route'}>
			<label>
				<input type="radio" value='Оптимальный' name="route" defaultChecked/>
				Оптимальный
			</label>
			<label>
				<input type="radio" value='Кратчайший' name="route" />
				Кратчайший
			</label>
			<label>
				<input type="radio" value='По региональным дорогам' name="route" />
				По региональным дорогам
			</label>
		</div>
	);
};



export default InputRoute;