import {GET_BASE_INFO} from "../actionTypes";

const initialState = {
	startCity: '',
	startCompany: '',
	finishCity: '',
	finishCompany: '',
	server: '',
	meetTime: '',
	outputTime: ''
};

export const baseInfo = (state = initialState, {type, payload}) => {
	switch (type) {
		case GET_BASE_INFO:
			return arrToObj(textToArr(payload), {...state});
		default:
			return state;
	}
};

const textToArr = (value) => {
	const result = value.split('\n');
	return cutNotNeeded(result)
};

const cutNotNeeded = arr => {
	arr.splice(0, 1);
	arr.splice(2, 4);
	arr.splice(4, 1);
	arr.splice(7);
	return arr;
};

const arrToObj = (arr, obj) => {
	let n = 0;

	for(let key in obj) {
		obj[key] = arr[n];
		n++
	}

	return obj;
};