import React, {Component} from 'react';

import './checkbox-dlc.scss';

class CheckboxDLC extends Component {

	state = {
	  game: false
	};

	handleCheck = (e) => {
		this.setState({
			game: e.target.checked
		});
		this.followGame();
	};

	followGame = () => {
		return (!this.state.game) ? this.renderListDLC(ets) : this.renderListDLC(ats);
	};

	renderListDLC = (arr) => {
		return (
			arr.map(({id, dlc}) => {
				return (
					<label key={id}>
						<input type="checkbox" value={dlc} name="dlc" />
						{ dlc }
					</label>
				);
			})
		);
	};

	render() {
		return (
			<div className={'checkbox-dlc'}>

				<input type="checkbox"
							 className="check"
							 value="true"
							 name="game"
							 id="checkbox"
							 onChange={this.handleCheck}/>
				<label className="check__label" htmlFor="checkbox">
					<span className="check__text check__text_on">ATS</span>
					<span className="check__text check__text_off">ETS</span>
				</label>

				<div className="checkbox-dlc__list">
					{
						this.followGame()
					}
				</div>
			</div>
		);
	}
}

export default CheckboxDLC

const ets = [
	{
		id: 1,
		dlc: "Beyond the Baltic Sea"
	},
	{
		id: 2,
		dlc: "Italia"
	},
	{
		id: 3,
		dlc: "Going East!"
	},
	{
		id: 4,
		dlc: "Scandinavia"
	},
	{
		id: 5,
		dlc: "Vive la France !"
	},
];

const ats = [
	{
		id: 1,
		dlc: "New Mexico"
	},
	{
		id: 2,
		dlc: "Oregon"
	}
];