import React, {Component} from 'react';
import {connect} from "react-redux";

class DiscordInfo extends Component {

	render() {

		const {
			startCity,
			meetTime
		} = this.props.baseInfo;

		return (
			<div className="info__block">
				<pre>@everyone</pre>
				<pre>Старт: {startCity}</pre>
				<pre>{meetTime}</pre>
				<pre>Подробнее:</pre>
				<pre>https://vk.com/vtcda</pre>
				<pre>{this.props.link}</pre>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		baseInfo: state.baseInfo,
		link: state.link
	}
};

export default connect(mapStateToProps)(DiscordInfo);