import React, {Component} from 'react';
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import './app.scss';

import {
	getBaseInfo,
	getExtendInfo,
	deleteExtendInfo,
	getDLC,
	deleteDLC,
	getRoute,
	getDefaultDiscord,
	getOtherDiscord,
	getDefaultCargo,
	getCargo,
	getLink
} from '../../actions';


import InputBaseInfo from "../input-base-info";
import VkInfo from "../vk-info";
import InputExtensionInfo from "../input-extension-info";
import CheckboxDLC from "../checkbox-dlc";
import InputRoute from "../input-route";
import InputDiscord from "../input-discord";
import InputCargo from "../input-cargo";
import InputLink from "../input-link";
import DiscordInfo from "../discord-info";


class App extends Component {

	handleSubmit = (e) => {
		e.preventDefault();

		const { extension, info, dlc, route, discord, cargo, link} = this._form;
		const {
			getBaseInfo,
			getExtendInfo,
			deleteExtendInfo,
			getDLC,
			deleteDLC,
			getRoute,
			getDefaultDiscord,
			getOtherDiscord,
			getDefaultCargo,
			getCargo,
			getLink
		} = this.props;

		// Ссылка на конвой
		getLink(link.value);
		// Основная информация
		getBaseInfo(info.value);
		// Предпочитаемый маршрут
		getRoute(route.value);
		// Доп.информ
		(extension.value) ? getExtendInfo(extension.value) : deleteExtendInfo();
		// Discord
		(!discord.value) ? getDefaultDiscord() : getOtherDiscord(discord.value);
		// Груз
		(!cargo.value) ? getDefaultCargo() : getCargo(cargo.value);

		// DLC
		const checkboxArray = Array.prototype.slice.call(dlc);
		const checkedCheckboxes = checkboxArray.filter(input => input.checked);
		const checkedCheckboxesValues = checkedCheckboxes.map(input => input.value);
		if(checkedCheckboxesValues.length !== 0) {
			getDLC(checkedCheckboxesValues)
		} else {
			deleteDLC();
		}



	};

	render() {
		return (
			<div className="container">
				<form
					onSubmit={this.handleSubmit}
					ref={form => this._form = form}>
					<div className="form__textarea-block">
						<InputBaseInfo />
						<InputExtensionInfo />
					</div>
					<div className="input__box">
						<div className="input__block">
							<InputLink/>
							<InputCargo/>
							<InputDiscord />
							<button className="btn">Создать</button>
						</div>
						<div className="input__block">
							<InputRoute />
							<CheckboxDLC />
						</div>
					</div>
				</form>
				<div className="info__box">
					<VkInfo />
					<DiscordInfo/>
				</div>
				<footer className="footer">
					<textarea placeholder="VK"/>
					<textarea placeholder="Discord"/>
				</footer>
			</div>
		);
	}
}


const matchDispatchToProps = dispatch => {
	return bindActionCreators({
		getBaseInfo,
		getExtendInfo,
		deleteExtendInfo,
		getDLC,
		deleteDLC,
		getRoute,
		getDefaultDiscord,
		getOtherDiscord,
		getCargo,
		getDefaultCargo,
		getLink
	}, dispatch)
};

export default connect(null, matchDispatchToProps)(App)