import { GET_DEFAULT_DISCORD, GET_OTHER_DISCORD } from "../actionTypes";

export const getDefaultDiscord = () => {
	return {
		type: GET_DEFAULT_DISCORD,
		payload: 'Связь: https://discord.gg/JSGZVph'
	}
};

export const getOtherDiscord = (value) => {
	return {
		type: GET_OTHER_DISCORD,
		payload: `Связь: ${value}`
	}
};

