import React, {Component} from 'react';
import {connect} from "react-redux";

class VkInfo extends Component {

	renderList = (arr) => {
		return (
			arr.map((item, id) => {
				return (
					<pre key={id}>{item}</pre>
				)
			})
		);
	};

	render() {

		const {
			startCity,
			startCompany,
			finishCity,
			finishCompany,
			server,
			meetTime,
			outputTime
		} = this.props.baseInfo;

		const { extensionInfo } = this.props;
		const { dlc } = this.props;

		return (
			<div className="info__block">
				<pre>{ server }</pre>
				<pre>{this.props.discord}</pre>
				<pre>{this.props.route}</pre>
				<br/>
				{this.renderList(extensionInfo)}
				{this.renderList(dlc)}
				<pre>Старт: { startCity } ( {startCompany} )</pre>
				<pre>Финиш: { finishCity } ( {finishCompany} )</pre>
				<pre>{ meetTime }</pre>
				<pre>{ outputTime }</pre>
				<pre>{ this.props.cargo }</pre>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		baseInfo: state.baseInfo,
		extensionInfo: state.extensionInfo,
		dlc: state.dlc,
		route: state.route,
		discord: state.discord,
		cargo: state.cargo
	}
};

export default connect(mapStateToProps)(VkInfo);