import {DELETE_EXTENSION_INFO, GET_EXTENSION_INFO} from "../actionTypes";

const initialState = [];

export const extensionInfo = (state = initialState, {type, payload}) => {
	switch (type) {
		case GET_EXTENSION_INFO:
			return textToArr(payload);
		case DELETE_EXTENSION_INFO:
			return [];
		default:
			return state;
	}
};

const textToArr = (value) => {
	return value.split('\n')
};